import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";

export const yieldHistoryAction: Action = {
  name: "YIELD_HISTORY",
  similes: [
    "HISTORICAL_PERFORMANCE",
    "PAST_RETURNS",
    "VAULT_HISTORY",
    "PERFORMANCE_HISTORY"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const historyKeywords = [
      'yield history', 'historical', 'past performance',
      'how has', 'performed', 'performance history',
      'returns history', 'past returns'
    ];

    return historyKeywords.some(keyword => content.includes(keyword));
  },

  description: "Show historical yield performance for a vault",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Yield History Action triggered");

      const vaultProvider = new VaultProvider(runtime);
      const content = message.content?.text || '';

      // Extract vault name
      const vaultName = extractVaultName(content);

      if (!vaultName) {
        if (callback) {
          callback({
            text: "Please specify which vault's history you want to see. Example: 'Show yield history for Delta Neutral'",
            content: { error: 'No vault specified', action: 'YIELD_HISTORY' }
          });
        }
        return;
      }

      const vaultAddress = await vaultProvider.getVaultAddressByName(vaultName);
      if (!vaultAddress) {
        if (callback) {
          callback({
            text: `I couldn't find the ${vaultName} vault.`,
            content: { error: 'Vault not found', action: 'YIELD_HISTORY' }
          });
        }
        return;
      }

      // Get history for last 30 days
      const thirtyDaysAgo = Math.floor(Date.now() / 1000) - (30 * 24 * 60 * 60);
      const history = await vaultProvider.getYieldHistory(vaultAddress, thirtyDaysAgo);

      if (history.length === 0) {
        if (callback) {
          callback({
            text: `No yield history available for ${vaultName} vault yet.`,
            content: { action: 'YIELD_HISTORY' }
          });
        }
        return;
      }

      // Calculate summary
      const avgApy = history.reduce((sum, h) => sum + h.apy, 0) / history.length;
      const totalYield = history.reduce((sum, h) => sum + parseFloat(h.yieldGenerated), 0);
      const trend = history.length > 1 && history[0].apy > history[history.length - 1].apy ? ' Improving' : ' Declining';

      let response = `${vaultName} Vault - 30 Day Performance:\n\n`;

      response += ` Summary:\n`;
      response += ` Average APY: ${avgApy.toFixed(1)}%\n`;
      response += ` Total Yield: $${totalYield.toLocaleString('en-US', { minimumFractionDigits: 2 })}\n`;
      response += ` Trend: ${trend}\n\n`;

      response += `Recent History:\n`;
      const recentHistory = history.slice(0, 5);
      for (const entry of recentHistory) {
        const date = new Date(entry.timestamp * 1000).toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
        response += ` ${date}: APY ${entry.apy.toFixed(1)}%, Value $${formatLargeNumber(parseFloat(entry.totalValue))}, Yield $${parseFloat(entry.yieldGenerated).toFixed(2)}\n`;
      }

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'YIELD_HISTORY',
            history,
            summary: { avgApy, totalYield }
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in yield history: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error fetching yield history. Please try again.",
          content: { error: error instanceof Error ? error.message : 'Unknown error', action: 'YIELD_HISTORY' }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "Show yield history for Delta Neutral" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Delta Neutral Vault - 30 Day Performance:\n\n Summary:\n Average APY: 11.8%\n Total Yield: $12,450\n Trend:  Improving\n\nRecent History:\n Nov 22: APY 12.5%, Value $1.23M, Yield $420\n Nov 21: APY 12.3%, Value $1.22M, Yield $415"
        }
      }
    ]
  ]
};

function extractVaultName(text: string): string | null {
  const vaultPatterns = [
    { pattern: /delta[\s-]?neutral/i, name: 'Delta Neutral' },
    { pattern: /stable[\s-]?max/i, name: 'Stable Max' },
    { pattern: /sei[\s-]?hypergrowth|hypergrowth/i, name: 'SEI Hypergrowth' },
    { pattern: /blue[\s-]?chip/i, name: 'Blue Chip' },
    { pattern: /hedge/i, name: 'Hedge' },
    { pattern: /yield[\s-]?farming|farming/i, name: 'Yield Farming' },
    { pattern: /arbitrage/i, name: 'Arbitrage' },
    { pattern: /concentrated/i, name: 'Concentrated Liquidity' },
    { pattern: /\bsei\s+vault\b/i, name: 'SEI' },
    { pattern: /\busdc\s+vault\b/i, name: 'USDC' }
  ];

  for (const { pattern, name } of vaultPatterns) {
    if (pattern.test(text)) return name;
  }
  return null;
}

function formatLargeNumber(num: number): string {
  if (num >= 1000000) return (num / 1000000).toFixed(2) + 'M';
  if (num >= 1000) return (num / 1000).toFixed(2) + 'K';
  return num.toFixed(2);
}
