// Vault types matching Solidity structs from IStrategyVault and ICustomerVaultDashboard

/**
 * Vault names enum for type safety
 */
export enum VaultName {
  SEI = 'SEI',
  USDC = 'USDC',
  DELTA_NEUTRAL = 'Delta Neutral',
  STABLE_MAX = 'Stable Max',
  SEI_HYPERGROWTH = 'SEI Hypergrowth',
  BLUE_CHIP = 'Blue Chip',
  HEDGE = 'Hedge',
  YIELD_FARMING = 'Yield Farming',
  ARBITRAGE = 'Arbitrage',
  CONCENTRATED_LIQUIDITY = 'Concentrated Liquidity'
}

/**
 * Vault identifier for fuzzy matching
 */
export const VAULT_IDENTIFIERS: Record<string, VaultName> = {
  'sei': VaultName.SEI,
  'sei vault': VaultName.SEI,
  'main': VaultName.SEI,
  'main vault': VaultName.SEI,
  'usdc': VaultName.USDC,
  'usdc vault': VaultName.USDC,
  'stablecoin': VaultName.USDC,
  'delta neutral': VaultName.DELTA_NEUTRAL,
  'delta-neutral': VaultName.DELTA_NEUTRAL,
  'deltaneutral': VaultName.DELTA_NEUTRAL,
  'dn': VaultName.DELTA_NEUTRAL,
  'stable max': VaultName.STABLE_MAX,
  'stablemax': VaultName.STABLE_MAX,
  'stable': VaultName.STABLE_MAX,
  'sei hypergrowth': VaultName.SEI_HYPERGROWTH,
  'hypergrowth': VaultName.SEI_HYPERGROWTH,
  'sei growth': VaultName.SEI_HYPERGROWTH,
  'seihypergrowth': VaultName.SEI_HYPERGROWTH,
  'blue chip': VaultName.BLUE_CHIP,
  'bluechip': VaultName.BLUE_CHIP,
  'hedge': VaultName.HEDGE,
  'hedging': VaultName.HEDGE,
  'yield farming': VaultName.YIELD_FARMING,
  'yieldfarming': VaultName.YIELD_FARMING,
  'farming': VaultName.YIELD_FARMING,
  'arbitrage': VaultName.ARBITRAGE,
  'arb': VaultName.ARBITRAGE,
  'concentrated liquidity': VaultName.CONCENTRATED_LIQUIDITY,
  'concentratedliquidity': VaultName.CONCENTRATED_LIQUIDITY,
  'concentrated': VaultName.CONCENTRATED_LIQUIDITY,
  'cl': VaultName.CONCENTRATED_LIQUIDITY
};

/**
 * From IStrategyVault.VaultInfo
 */
export interface VaultInfo {
  name: string;
  strategy: string;
  token0: string;
  token1: string;
  poolFee: number;
  totalSupply: bigint;
  totalValueLocked: bigint;
  isActive: boolean;
}

/**
 * From IStrategyVault.Position
 */
export interface Position {
  tickLower: number;
  tickUpper: number;
  liquidity: bigint;
  tokensOwed0: bigint;
  tokensOwed1: bigint;
  feeGrowthInside0LastX128: bigint;
  feeGrowthInside1LastX128: bigint;
}

/**
 * From IStrategyVault.AIRebalanceParams
 */
export interface AIRebalanceParams {
  newTickLower: number;
  newTickUpper: number;
  minAmount0: bigint;
  minAmount1: bigint;
  deadline: bigint;
  aiSignature: `0x${string}`;
}

/**
 * From ICustomerVaultDashboard.CustomerPortfolio
 */
export interface CustomerPortfolio {
  vaultAddress: string;
  vaultName: string;
  shareBalance: bigint;
  shareValue: bigint;
  totalDeposited: bigint;
  totalWithdrawn: bigint;
  unrealizedGains: bigint;
  depositTimestamp: bigint;
  lockTimeRemaining: bigint;
  canWithdraw: boolean;
}

/**
 * From ICustomerVaultDashboard.VaultMetrics
 */
export interface VaultMetrics {
  totalValueLocked: bigint;
  totalShares: bigint;
  pricePerShare: bigint;
  apy: bigint;
  totalYieldGenerated: bigint;
  managementFeeRate: bigint;
  performanceFeeRate: bigint;
  withdrawalFeeRate: bigint;
}

/**
 * From ICustomerVaultDashboard.YieldHistory
 */
export interface YieldHistory {
  timestamp: bigint;
  totalValue: bigint;
  yieldGenerated: bigint;
  apy: bigint;
}

/**
 * Projected returns response
 */
export interface ProjectedReturns {
  projectedValue: bigint;
  projectedYield: bigint;
  managementFees: bigint;
  withdrawalFees: bigint;
}

/**
 * Optimal deposit ratio response
 */
export interface DepositRatio {
  token0Ratio: bigint;
  token1Ratio: bigint;
  currentPrice: bigint;
}

/**
 * Formatted versions for display (converted from bigint to numbers/strings)
 */
export interface FormattedCustomerPortfolio {
  vaultAddress: string;
  vaultName: string;
  shareBalance: string;
  shareValue: string;
  totalDeposited: string;
  totalWithdrawn: string;
  unrealizedGains: string;
  depositTimestamp: number;
  lockTimeRemaining: number;
  canWithdraw: boolean;
}

export interface FormattedVaultMetrics {
  totalValueLocked: string;
  totalShares: string;
  pricePerShare: string;
  apy: number;
  totalYieldGenerated: string;
  managementFeeRate: number;
  performanceFeeRate: number;
  withdrawalFeeRate: number;
}

export interface FormattedYieldHistory {
  timestamp: number;
  totalValue: string;
  yieldGenerated: string;
  apy: number;
}

export interface FormattedProjectedReturns {
  projectedValue: string;
  projectedYield: string;
  managementFees: string;
  withdrawalFees: string;
}

export interface FormattedVaultInfo {
  name: string;
  strategy: string;
  token0: string;
  token1: string;
  poolFee: number;
  totalSupply: string;
  totalValueLocked: string;
  isActive: boolean;
}

/**
 * Vault with address for listing
 */
export interface VaultWithAddress extends FormattedVaultInfo {
  address: string;
  apy?: number;
  riskLevel?: 'Very Low' | 'Low' | 'Medium' | 'Medium-High' | 'High';
}

/**
 * Helper function to match vault name from user input
 */
export function matchVaultName(input: string): VaultName | null {
  const normalized = input.toLowerCase().trim();
  return VAULT_IDENTIFIERS[normalized] || null;
}

/**
 * Type guard for VaultInfo
 */
export function isVaultInfo(obj: unknown): obj is VaultInfo {
  return (
    typeof obj === 'object' &&
    obj !== null &&
    'name' in obj &&
    'strategy' in obj &&
    'totalValueLocked' in obj
  );
}

/**
 * Type guard for CustomerPortfolio
 */
export function isCustomerPortfolio(obj: unknown): obj is CustomerPortfolio {
  return (
    typeof obj === 'object' &&
    obj !== null &&
    'vaultAddress' in obj &&
    'shareBalance' in obj &&
    'shareValue' in obj
  );
}

/**
 * Type guard for VaultMetrics
 */
export function isVaultMetrics(obj: unknown): obj is VaultMetrics {
  return (
    typeof obj === 'object' &&
    obj !== null &&
    'totalValueLocked' in obj &&
    'apy' in obj &&
    'pricePerShare' in obj
  );
}
