import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";
import { matchVaultName, VaultName } from "../types/vault";

export const vaultMetricsAction: Action = {
  name: "VAULT_METRICS",
  similes: [
    "GET_VAULT_STATS",
    "VAULT_APY",
    "VAULT_TVL",
    "VAULT_PERFORMANCE",
    "VAULT_INFO"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const metricsKeywords = [
      'apy of', 'apy for', 'tvl of', 'tvl for',
      'vault stats', 'vault metrics', 'vault performance',
      'fees for', 'how is the', 'performing',
      'what are the fees', 'management fee', 'performance fee'
    ];

    // Also check if they mention a specific vault
    const vaultNames = [
      'delta neutral', 'stable max', 'hypergrowth', 'blue chip',
      'hedge', 'yield farming', 'arbitrage', 'concentrated', 'sei vault', 'usdc vault'
    ];

    const mentionsVault = vaultNames.some(name => content.includes(name));
    const hasKeyword = metricsKeywords.some(keyword => content.includes(keyword));

    return hasKeyword || (mentionsVault && (content.includes('apy') || content.includes('tvl') || content.includes('fee') || content.includes('stat') || content.includes('metric')));
  },

  description: "Get performance metrics for a specific Yield Delta vault",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Vault Metrics Action triggered");

      const vaultProvider = new VaultProvider(runtime);
      const content = message.content?.text || '';

      // Extract vault name from message
      const vaultName = extractVaultName(content);

      if (!vaultName) {
        if (callback) {
          callback({
            text: "I couldn't identify which vault you're asking about. Please specify a vault name like 'Delta Neutral', 'Stable Max', 'SEI Hypergrowth', etc.",
            content: {
              error: 'No vault identified',
              action: 'VAULT_METRICS'
            }
          });
        }
        return;
      }

      // Get vault address
      const vaultAddress = await vaultProvider.getVaultAddressByName(vaultName);
      if (!vaultAddress) {
        if (callback) {
          callback({
            text: `I couldn't find the address for ${vaultName} vault. Please check the vault name and try again.`,
            content: {
              error: 'Vault address not found',
              action: 'VAULT_METRICS'
            }
          });
        }
        return;
      }

      // Get metrics and info
      const [metrics, info] = await Promise.all([
        vaultProvider.getVaultMetrics(vaultAddress),
        vaultProvider.getVaultInfo(vaultAddress)
      ]);

      // Format response
      let response = `${info.name} Vault Metrics:\n\n`;

      response += ` Performance:\n`;
      response += ` APY: ${metrics.apy.toFixed(2)}%\n`;
      response += ` Total Yield Generated: $${formatLargeNumber(parseFloat(metrics.totalYieldGenerated))}\n\n`;

      response += ` TVL & Shares:\n`;
      response += ` Total Value Locked: $${formatLargeNumber(parseFloat(metrics.totalValueLocked))}\n`;
      response += ` Total Shares: ${formatLargeNumber(parseFloat(metrics.totalShares))}\n`;
      response += ` Price per Share: $${parseFloat(metrics.pricePerShare).toFixed(4)}\n\n`;

      response += ` Fees:\n`;
      response += ` Management: ${(metrics.managementFeeRate * 100).toFixed(2)}%\n`;
      response += ` Performance: ${(metrics.performanceFeeRate * 100).toFixed(2)}%\n`;
      response += ` Withdrawal: ${(metrics.withdrawalFeeRate * 100).toFixed(2)}%\n\n`;

      response += `Strategy: ${info.strategy}`;

      if (!info.isActive) {
        response += `\n\n Note: This vault is currently not accepting new deposits.`;
      }

      elizaLogger.info(`Vault metrics response generated for ${info.name}`);

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'VAULT_METRICS',
            vaultName: info.name,
            metrics,
            info
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in vault metrics: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error while fetching vault metrics. Please try again in a moment.",
          content: {
            error: error instanceof Error ? error.message : 'Unknown error',
            action: 'VAULT_METRICS'
          }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "What's the APY of Delta Neutral vault?" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Delta Neutral Vault Metrics:\n\n Performance:\n APY: 12.50%\n Total Yield Generated: $45,230\n\n TVL & Shares:\n Total Value Locked: $1,234,567\n Total Shares: 50,000\n Price per Share: $24.69\n\n Fees:\n Management: 0.50%\n Performance: 10.00%\n Withdrawal: 0.10%\n\nStrategy: Delta-neutral yield farming with IL protection"
        }
      }
    ],
    [
      {
        name: "{{user1}}",
        content: { text: "Show TVL for Stable Max" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Stable Max Vault Metrics:\n\n Performance:\n APY: 8.20%\n Total Yield Generated: $28,150\n\n TVL & Shares:\n Total Value Locked: $890,000\n Total Shares: 85,000\n Price per Share: $10.47\n\n Fees:\n Management: 0.30%\n Performance: 5.00%\n Withdrawal: 0.05%\n\nStrategy: Stablecoin yield optimization"
        }
      }
    ]
  ]
};

/**
 * Extract vault name from message text
 */
function extractVaultName(text: string): string | null {
  const lowerText = text.toLowerCase();

  // Check for specific vault names
  const vaultPatterns = [
    { pattern: /delta[\s-]?neutral/i, name: 'Delta Neutral' },
    { pattern: /stable[\s-]?max/i, name: 'Stable Max' },
    { pattern: /sei[\s-]?hypergrowth|hypergrowth/i, name: 'SEI Hypergrowth' },
    { pattern: /blue[\s-]?chip/i, name: 'Blue Chip' },
    { pattern: /hedge|hedging/i, name: 'Hedge' },
    { pattern: /yield[\s-]?farming|farming/i, name: 'Yield Farming' },
    { pattern: /arbitrage|arb\b/i, name: 'Arbitrage' },
    { pattern: /concentrated[\s-]?liquidity|concentrated|\bcl\b/i, name: 'Concentrated Liquidity' },
    { pattern: /\bsei\s+vault\b|\bmain\s+vault\b/i, name: 'SEI' },
    { pattern: /\busdc\s+vault\b|\bstablecoin\s+vault\b/i, name: 'USDC' }
  ];

  for (const { pattern, name } of vaultPatterns) {
    if (pattern.test(text)) {
      return name;
    }
  }

  return null;
}

/**
 * Format large numbers
 */
function formatLargeNumber(num: number): string {
  if (num >= 1000000) {
    return (num / 1000000).toFixed(2) + 'M';
  } else if (num >= 1000) {
    return (num / 1000).toFixed(2) + 'K';
  }
  return num.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
}
