import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";
import { formatUnits } from 'viem';

export const positionDetailsAction: Action = {
  name: "POSITION_DETAILS",
  similes: [
    "VAULT_POSITION",
    "LP_POSITION",
    "TICK_RANGE",
    "LIQUIDITY_DETAILS"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const positionKeywords = [
      'position for', 'position of', 'current position',
      'tick range', 'liquidity details', 'lp position',
      'position info', 'show position'
    ];

    return positionKeywords.some(keyword => content.includes(keyword));
  },

  description: "Show detailed position information for a vault (tick ranges, liquidity)",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Position Details Action triggered");

      const vaultProvider = new VaultProvider(runtime);
      const content = message.content?.text || '';

      const vaultName = extractVaultName(content);

      if (!vaultName) {
        if (callback) {
          callback({
            text: "Please specify which vault's position you want to see. Example: 'Show position for Delta Neutral'",
            content: { error: 'No vault specified', action: 'POSITION_DETAILS' }
          });
        }
        return;
      }

      const vaultAddress = await vaultProvider.getVaultAddressByName(vaultName);
      if (!vaultAddress) {
        if (callback) {
          callback({
            text: `I couldn't find the ${vaultName} vault.`,
            content: { error: 'Vault not found', action: 'POSITION_DETAILS' }
          });
        }
        return;
      }

      const [position, info] = await Promise.all([
        vaultProvider.getCurrentPosition(vaultAddress),
        vaultProvider.getVaultInfo(vaultAddress)
      ]);

      // Calculate prices from ticks (approximation)
      const tickToPrice = (tick: number) => Math.pow(1.0001, tick);
      const lowerPrice = tickToPrice(position.tickLower);
      const upperPrice = tickToPrice(position.tickUpper);
      const midPrice = (lowerPrice + upperPrice) / 2;

      let response = `${info.name} Vault - Current Position:\n\n`;

      response += ` Tick Range:\n`;
      response += ` Lower: ${position.tickLower} (price: $${lowerPrice.toFixed(4)})\n`;
      response += ` Upper: ${position.tickUpper} (price: $${upperPrice.toFixed(4)})\n`;
      response += ` Mid: (price: $${midPrice.toFixed(4)})\n\n`;

      response += ` Liquidity:\n`;
      response += ` Total Liquidity: ${formatUnits(position.liquidity, 0)}\n`;
      response += ` Token0 Owed: ${formatUnits(position.tokensOwed0, 18)}\n`;
      response += ` Token1 Owed: ${formatUnits(position.tokensOwed1, 18)}\n\n`;

      response += ` Uncollected Fees:\n`;
      response += ` Token0: ${formatUnits(position.tokensOwed0, 18)}\n`;
      response += ` Token1: ${formatUnits(position.tokensOwed1, 18)}`;

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'POSITION_DETAILS',
            position,
            vaultName
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in position details: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error fetching position details. Please try again.",
          content: { error: error instanceof Error ? error.message : 'Unknown error', action: 'POSITION_DETAILS' }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "Show position for Delta Neutral" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Delta Neutral Vault - Current Position:\n\n Tick Range:\n Lower: -887220 (price: $0.38)\n Upper: -886380 (price: $0.52)\n Mid: (price: $0.45)\n\n Liquidity:\n Total Liquidity: 1234567890\n Token0 Owed: 50000\n Token1 Owed: 22500\n\n Uncollected Fees:\n Token0: 125.5\n Token1: 56.2"
        }
      }
    ]
  ]
};

function extractVaultName(text: string): string | null {
  const patterns = [
    { pattern: /delta[\s-]?neutral/i, name: 'Delta Neutral' },
    { pattern: /stable[\s-]?max/i, name: 'Stable Max' },
    { pattern: /hypergrowth/i, name: 'SEI Hypergrowth' },
    { pattern: /blue[\s-]?chip/i, name: 'Blue Chip' },
    { pattern: /hedge/i, name: 'Hedge' },
    { pattern: /farming/i, name: 'Yield Farming' },
    { pattern: /arbitrage/i, name: 'Arbitrage' },
    { pattern: /concentrated/i, name: 'Concentrated Liquidity' },
    { pattern: /\bsei\s+vault\b/i, name: 'SEI' },
    { pattern: /\busdc\s+vault\b/i, name: 'USDC' }
  ];

  for (const { pattern, name } of patterns) {
    if (pattern.test(text)) return name;
  }
  return null;
}
