import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";

export const vaultListAction: Action = {
  name: "VAULT_LIST",
  similes: [
    "LIST_VAULTS",
    "SHOW_VAULTS",
    "AVAILABLE_VAULTS",
    "ALL_VAULTS",
    "VAULT_OPTIONS"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const listKeywords = [
      'what vaults', 'which vaults', 'available vaults', 'list vaults',
      'show vaults', 'all vaults', 'vault options', 'what strategies',
      'which strategies', 'what can i invest', 'investment options',
      'show me the vaults', 'what are my options'
    ];

    return listKeywords.some(keyword => content.includes(keyword));
  },

  description: "List all available Yield Delta vaults with key metrics",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Vault List Action triggered");

      const vaultProvider = new VaultProvider(runtime);

      // Get all vaults
      const vaults = await vaultProvider.getAllVaults();

      if (vaults.length === 0) {
        if (callback) {
          callback({
            text: "No vaults are currently available. Please check back later or ensure the vault contracts are properly configured.",
            content: {
              error: 'No vaults found',
              action: 'VAULT_LIST'
            }
          });
        }
        return;
      }

      // Format response
      let response = `Available Yield Delta Vaults:\n\n`;

      for (const vault of vaults) {
        const riskEmoji = getRiskEmoji(vault.riskLevel || 'Medium');
        const tvlFormatted = formatLargeNumber(parseFloat(vault.totalValueLocked));
        const apyFormatted = vault.apy ? vault.apy.toFixed(1) : 'N/A';

        response += `${riskEmoji} ${vault.name}\n`;
        response += `   APY: ${apyFormatted}% | TVL: $${tvlFormatted} | Risk: ${vault.riskLevel || 'Medium'}\n`;
        response += `   Strategy: ${vault.strategy}\n\n`;
      }

      response += `Use 'vault metrics [name]' for detailed info on any vault.`;

      elizaLogger.info(`Vault list response generated for ${vaults.length} vaults`);

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'VAULT_LIST',
            vaults,
            totalVaults: vaults.length
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in vault list: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error while fetching the vault list. Please try again in a moment.",
          content: {
            error: error instanceof Error ? error.message : 'Unknown error',
            action: 'VAULT_LIST'
          }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "What vaults are available?" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Available Yield Delta Vaults:\n\n Delta Neutral Vault\n   APY: 12.5% | TVL: $1.23M | Risk: Low\n   Strategy: Delta-neutral yield with IL protection\n\n Stable Max Vault\n   APY: 8.2% | TVL: $890K | Risk: Very Low\n   Strategy: Stablecoin optimization\n\n SEI Hypergrowth Vault\n   APY: 24.8% | TVL: $456K | Risk: High\n   Strategy: Leveraged SEI exposure\n\nUse 'vault metrics [name]' for detailed info on any vault."
        }
      }
    ],
    [
      {
        name: "{{user1}}",
        content: { text: "Show all vaults" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Available Yield Delta Vaults:\n\n SEI Vault\n   APY: 10.5% | TVL: $2.1M | Risk: Medium\n   Strategy: Core SEI yield optimization\n\n USDC Vault\n   APY: 6.8% | TVL: $1.5M | Risk: Very Low\n   Strategy: Stablecoin yield farming\n\nUse 'vault metrics [name]' for detailed info on any vault."
        }
      }
    ]
  ]
};

/**
 * Get emoji based on risk level
 */
function getRiskEmoji(riskLevel: string): string {
  switch (riskLevel) {
    case 'Very Low':
      return '';
    case 'Low':
      return '';
    case 'Medium':
      return '';
    case 'Medium-High':
      return '';
    case 'High':
      return '';
    default:
      return '';
  }
}

/**
 * Format large numbers
 */
function formatLargeNumber(num: number): string {
  if (num >= 1000000) {
    return (num / 1000000).toFixed(2) + 'M';
  } else if (num >= 1000) {
    return (num / 1000).toFixed(2) + 'K';
  }
  return num.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
}
