import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";

export const projectedReturnsAction: Action = {
  name: "PROJECTED_RETURNS",
  similes: [
    "CALCULATE_RETURNS",
    "ESTIMATE_YIELD",
    "PROJECT_EARNINGS",
    "EXPECTED_RETURNS"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const returnsKeywords = [
      'what would i earn', 'calculate returns', 'project returns',
      'projected returns', 'estimate yield', 'how much would i make',
      'expected returns', 'if i deposit', 'earnings for'
    ];

    return returnsKeywords.some(keyword => content.includes(keyword));
  },

  description: "Calculate projected returns for a deposit into a vault",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Projected Returns Action triggered");

      const vaultProvider = new VaultProvider(runtime);
      const content = message.content?.text || '';

      // Extract parameters
      const params = extractProjectionParams(content);

      if (!params.vaultName) {
        if (callback) {
          callback({
            text: "Please specify which vault you want to calculate returns for. Example: 'Calculate returns for 1000 SEI in Delta Neutral for 30 days'",
            content: { error: 'No vault specified', action: 'PROJECTED_RETURNS' }
          });
        }
        return;
      }

      // Get vault address
      const vaultAddress = await vaultProvider.getVaultAddressByName(params.vaultName);
      if (!vaultAddress) {
        if (callback) {
          callback({
            text: `I couldn't find the ${params.vaultName} vault. Please check the vault name.`,
            content: { error: 'Vault not found', action: 'PROJECTED_RETURNS' }
          });
        }
        return;
      }

      // Get projected returns and metrics
      const [projection, metrics] = await Promise.all([
        vaultProvider.calculateProjectedReturns(vaultAddress, params.amount, params.days),
        vaultProvider.getVaultMetrics(vaultAddress)
      ]);

      // Format response
      let response = `Projected Returns for ${params.amount.toLocaleString()} in ${params.vaultName} Vault (${params.days} days):\n\n`;

      response += ` Deposit: $${params.amount.toLocaleString('en-US', { minimumFractionDigits: 2 })}\n\n`;

      response += ` Projections (based on current ${metrics.apy.toFixed(1)}% APY):\n`;
      response += ` Projected Value: $${parseFloat(projection.projectedValue).toLocaleString('en-US', { minimumFractionDigits: 2 })}\n`;
      response += ` Gross Yield: $${parseFloat(projection.projectedYield).toLocaleString('en-US', { minimumFractionDigits: 2 })}\n`;
      response += ` Management Fees: -$${parseFloat(projection.managementFees).toLocaleString('en-US', { minimumFractionDigits: 2 })}\n`;

      const netYield = parseFloat(projection.projectedYield) - parseFloat(projection.managementFees);
      response += ` Net Yield: $${netYield.toLocaleString('en-US', { minimumFractionDigits: 2 })}\n\n`;

      response += ` On Withdrawal:\n`;
      response += ` Withdrawal Fee: -$${parseFloat(projection.withdrawalFees).toLocaleString('en-US', { minimumFractionDigits: 2 })}\n`;

      const finalValue = parseFloat(projection.projectedValue) - parseFloat(projection.withdrawalFees);
      response += ` Final Value: $${finalValue.toLocaleString('en-US', { minimumFractionDigits: 2 })}\n\n`;

      response += ` Note: Projections based on current APY. Actual returns may vary based on market conditions.`;

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'PROJECTED_RETURNS',
            projection,
            params
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in projected returns: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error calculating projected returns. Please try again.",
          content: { error: error instanceof Error ? error.message : 'Unknown error', action: 'PROJECTED_RETURNS' }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "What would I earn depositing 1000 into Delta Neutral for 30 days?" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Projected Returns for 1,000 in Delta Neutral Vault (30 days):\n\n Deposit: $1,000.00\n\n Projections (based on current 12.5% APY):\n Projected Value: $1,010.27\n Gross Yield: $10.27\n Management Fees: -$0.41\n Net Yield: $9.86\n\n On Withdrawal:\n Withdrawal Fee: -$1.01\n Final Value: $1,009.26\n\n Note: Projections based on current APY. Actual returns may vary based on market conditions."
        }
      }
    ]
  ]
};

function extractProjectionParams(text: string): { vaultName: string | null; amount: number; days: number } {
  const vaultPatterns = [
    { pattern: /delta[\s-]?neutral/i, name: 'Delta Neutral' },
    { pattern: /stable[\s-]?max/i, name: 'Stable Max' },
    { pattern: /sei[\s-]?hypergrowth|hypergrowth/i, name: 'SEI Hypergrowth' },
    { pattern: /blue[\s-]?chip/i, name: 'Blue Chip' },
    { pattern: /hedge/i, name: 'Hedge' },
    { pattern: /yield[\s-]?farming|farming/i, name: 'Yield Farming' },
    { pattern: /arbitrage/i, name: 'Arbitrage' },
    { pattern: /concentrated/i, name: 'Concentrated Liquidity' },
    { pattern: /\bsei\s+vault\b/i, name: 'SEI' },
    { pattern: /\busdc\s+vault\b/i, name: 'USDC' }
  ];

  let vaultName: string | null = null;
  for (const { pattern, name } of vaultPatterns) {
    if (pattern.test(text)) {
      vaultName = name;
      break;
    }
  }

  // Extract amount
  const amountMatch = text.match(/(\d+(?:,\d{3})*(?:\.\d+)?)\s*(?:sei|usdc|usd|\$)?/i);
  const amount = amountMatch ? parseFloat(amountMatch[1].replace(/,/g, '')) : 1000;

  // Extract days
  const daysMatch = text.match(/(\d+)\s*days?/i);
  const days = daysMatch ? parseInt(daysMatch[1]) : 30;

  return { vaultName, amount, days };
}
