import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";
import { WalletProvider } from "../providers/wallet";
import { FormattedCustomerPortfolio } from "../types/vault";

export const portfolioQueryAction: Action = {
  name: "PORTFOLIO_QUERY",
  similes: [
    "GET_PORTFOLIO",
    "CHECK_PORTFOLIO",
    "MY_POSITIONS",
    "VAULT_POSITIONS",
    "PORTFOLIO_STATUS"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const portfolioKeywords = [
      'my portfolio', 'my positions', 'my balance', 'my deposits',
      'portfolio value', 'what do i have', 'show my', 'my vault',
      'my gains', 'my returns', 'can i withdraw', 'withdrawal status',
      'how much have i', 'what\'s my', 'whats my'
    ];

    return portfolioKeywords.some(keyword => content.includes(keyword));
  },

  description: "Get user's portfolio status across all Yield Delta vaults",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Portfolio Query Action triggered");

      const vaultProvider = new VaultProvider(runtime);
      const walletProvider = new WalletProvider(runtime);

      // Get user's wallet address
      const walletAddress = walletProvider.getAddress();
      if (!walletAddress) {
        if (callback) {
          callback({
            text: "I couldn't find your wallet address. Please make sure your wallet is configured.",
            content: {
              error: 'No wallet address',
              action: 'PORTFOLIO_QUERY'
            }
          });
        }
        return;
      }

      elizaLogger.info(`Fetching portfolio for address: ${walletAddress}`);

      // Get portfolio data
      const portfolio = await vaultProvider.getCustomerPortfolio(walletAddress);

      if (portfolio.length === 0) {
        if (callback) {
          callback({
            text: "You don't have any positions in Yield Delta vaults yet. Visit our app to deposit into a vault and start earning yield!",
            content: {
              text: "No positions found",
              action: 'PORTFOLIO_QUERY',
              portfolio: []
            }
          });
        }
        return;
      }

      // Calculate totals
      const totalValue = portfolio.reduce((sum, p) => sum + parseFloat(p.shareValue), 0);
      const totalGains = portfolio.reduce((sum, p) => sum + parseFloat(p.unrealizedGains), 0);

      // Format response
      let response = `Your Yield Delta Portfolio:\n\n`;
      response += `Total Value: $${totalValue.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}\n\n`;
      response += `Positions:\n`;

      for (const position of portfolio) {
        const shares = parseFloat(position.shareBalance);
        const value = parseFloat(position.shareValue);
        const gains = parseFloat(position.unrealizedGains);
        const gainsSign = gains >= 0 ? '+' : '';
        const withdrawStatus = position.canWithdraw
          ? ' Can withdraw'
          : ` ${formatLockTime(position.lockTimeRemaining)} lock remaining`;

        response += ` ${position.vaultName}: ${formatNumber(shares)} shares ($${value.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}) ${gainsSign}$${gains.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })} gains ${withdrawStatus}\n`;
      }

      const totalGainsSign = totalGains >= 0 ? '+' : '';
      response += `\nTotal Unrealized Gains: ${totalGainsSign}$${totalGains.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;

      elizaLogger.info(`Portfolio query response generated for ${portfolio.length} positions`);

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'PORTFOLIO_QUERY',
            portfolio: portfolio,
            summary: {
              totalValue,
              totalGains,
              positionCount: portfolio.length
            }
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in portfolio query: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error while fetching your portfolio. Please try again in a moment.",
          content: {
            error: error instanceof Error ? error.message : 'Unknown error',
            action: 'PORTFOLIO_QUERY'
          }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "What's my portfolio value?" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Your Yield Delta Portfolio:\n\nTotal Value: $5,234.56\n\nPositions:\n Delta Neutral Vault: 1,000 shares ($2,100.00) +$100.00 gains  Can withdraw\n Stable Max Vault: 500 shares ($1,534.56) +$34.56 gains  2 days lock remaining\n\nTotal Unrealized Gains: +$134.56"
        }
      }
    ],
    [
      {
        name: "{{user1}}",
        content: { text: "Show my positions" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Your Yield Delta Portfolio:\n\nTotal Value: $10,000.00\n\nPositions:\n SEI Hypergrowth: 2,000 shares ($5,000.00) +$500.00 gains  Can withdraw\n Blue Chip Vault: 1,500 shares ($5,000.00) +$250.00 gains  Can withdraw\n\nTotal Unrealized Gains: +$750.00"
        }
      }
    ],
    [
      {
        name: "{{user1}}",
        content: { text: "Can I withdraw my funds?" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Your Yield Delta Portfolio:\n\nTotal Value: $3,000.00\n\nPositions:\n Hedge Vault: 1,000 shares ($3,000.00) +$150.00 gains  Can withdraw\n\nTotal Unrealized Gains: +$150.00"
        }
      }
    ]
  ]
};

/**
 * Format number with appropriate decimals
 */
function formatNumber(num: number): string {
  if (num >= 1000000) {
    return (num / 1000000).toFixed(2) + 'M';
  } else if (num >= 1000) {
    return (num / 1000).toFixed(2) + 'K';
  }
  return num.toLocaleString('en-US', { maximumFractionDigits: 2 });
}

/**
 * Format lock time remaining
 */
function formatLockTime(seconds: number): string {
  if (seconds <= 0) return '0';

  const days = Math.floor(seconds / 86400);
  const hours = Math.floor((seconds % 86400) / 3600);

  if (days > 0) {
    return `${days} day${days > 1 ? 's' : ''}`;
  } else if (hours > 0) {
    return `${hours} hour${hours > 1 ? 's' : ''}`;
  } else {
    const minutes = Math.floor(seconds / 60);
    return `${minutes} minute${minutes > 1 ? 's' : ''}`;
  }
}
