import {
  Action,
  IAgentRuntime,
  Memory,
  State,
  HandlerCallback,
  elizaLogger
} from "@elizaos/core";
import { VaultProvider } from "../providers/vault-provider";
import { formatUnits } from 'viem';

export const optimalDepositAction: Action = {
  name: "OPTIMAL_DEPOSIT",
  similes: [
    "DEPOSIT_RATIO",
    "BEST_RATIO",
    "HOW_TO_DEPOSIT",
    "DEPOSIT_RECOMMENDATION"
  ],

  validate: async (runtime: IAgentRuntime, message: Memory) => {
    const content = message.content?.text?.toLowerCase() || '';

    const depositKeywords = [
      'optimal deposit', 'deposit ratio', 'best ratio',
      'how should i deposit', 'deposit recommendation',
      'optimal ratio', 'how to deposit'
    ];

    return depositKeywords.some(keyword => content.includes(keyword));
  },

  description: "Get optimal deposit ratio for a vault",

  handler: async (
    runtime: IAgentRuntime,
    message: Memory,
    state?: State,
    _options?: { [key: string]: unknown },
    callback?: HandlerCallback
  ): Promise<void> => {
    try {
      elizaLogger.info("Optimal Deposit Action triggered");

      const vaultProvider = new VaultProvider(runtime);
      const content = message.content?.text || '';

      const vaultName = extractVaultName(content);

      if (!vaultName) {
        if (callback) {
          callback({
            text: "Please specify which vault. Example: 'What's the optimal deposit ratio for Delta Neutral?'",
            content: { error: 'No vault specified', action: 'OPTIMAL_DEPOSIT' }
          });
        }
        return;
      }

      const vaultAddress = await vaultProvider.getVaultAddressByName(vaultName);
      if (!vaultAddress) {
        if (callback) {
          callback({
            text: `I couldn't find the ${vaultName} vault.`,
            content: { error: 'Vault not found', action: 'OPTIMAL_DEPOSIT' }
          });
        }
        return;
      }

      const [ratio, info] = await Promise.all([
        vaultProvider.getOptimalDepositRatio(vaultAddress),
        vaultProvider.getVaultInfo(vaultAddress)
      ]);

      const token0Ratio = Number(ratio.token0Ratio);
      const token1Ratio = Number(ratio.token1Ratio);
      const total = token0Ratio + token1Ratio;
      const token0Pct = (token0Ratio / total * 100).toFixed(0);
      const token1Pct = (token1Ratio / total * 100).toFixed(0);
      const currentPrice = Number(formatUnits(ratio.currentPrice, 18));

      let response = `Optimal Deposit for ${info.name} Vault:\n\n`;

      response += ` Current Optimal Ratio:\n`;
      response += ` Token0: ${token0Pct}%\n`;
      response += ` Token1: ${token1Pct}%\n\n`;

      response += ` Current Price: ${currentPrice.toFixed(4)}\n\n`;

      response += ` Example Deposits:\n`;
      const examples = [1000, 5000, 10000];
      for (const amount of examples) {
        const token0Amount = (amount * token0Ratio / total).toFixed(2);
        const token1Amount = (amount * token1Ratio / total).toFixed(2);
        response += ` For $${amount.toLocaleString()}: $${token0Amount} Token0 + $${token1Amount} Token1\n`;
      }

      response += `\nDepositing at the optimal ratio maximizes your LP position efficiency and minimizes unused tokens.`;

      if (callback) {
        callback({
          text: response,
          content: {
            text: response,
            action: 'OPTIMAL_DEPOSIT',
            ratio: { token0Pct, token1Pct, currentPrice },
            vaultName
          }
        });
      }

    } catch (error) {
      elizaLogger.error(`Error in optimal deposit: ${error instanceof Error ? error.message : String(error)}`);

      if (callback) {
        callback({
          text: "I encountered an error fetching deposit ratios. Please try again.",
          content: { error: error instanceof Error ? error.message : 'Unknown error', action: 'OPTIMAL_DEPOSIT' }
        });
      }
    }
  },

  examples: [
    [
      {
        name: "{{user1}}",
        content: { text: "What's the optimal deposit ratio for Delta Neutral?" }
      },
      {
        name: "{{agentName}}",
        content: {
          text: "Optimal Deposit for Delta Neutral Vault:\n\n Current Optimal Ratio:\n Token0: 55%\n Token1: 45%\n\n Current Price: 0.4500\n\n Example Deposits:\n For $1,000: $550 Token0 + $450 Token1\n For $5,000: $2,750 Token0 + $2,250 Token1\n\nDepositing at the optimal ratio maximizes your LP position efficiency."
        }
      }
    ]
  ]
};

function extractVaultName(text: string): string | null {
  const patterns = [
    { pattern: /delta[\s-]?neutral/i, name: 'Delta Neutral' },
    { pattern: /stable[\s-]?max/i, name: 'Stable Max' },
    { pattern: /hypergrowth/i, name: 'SEI Hypergrowth' },
    { pattern: /blue[\s-]?chip/i, name: 'Blue Chip' },
    { pattern: /hedge/i, name: 'Hedge' },
    { pattern: /farming/i, name: 'Yield Farming' },
    { pattern: /arbitrage/i, name: 'Arbitrage' },
    { pattern: /concentrated/i, name: 'Concentrated Liquidity' },
    { pattern: /\bsei\s+vault\b/i, name: 'SEI' },
    { pattern: /\busdc\s+vault\b/i, name: 'USDC' }
  ];

  for (const { pattern, name } of patterns) {
    if (pattern.test(text)) return name;
  }
  return null;
}
