const VAULT_FACTORY_ABI = [
  {
    name: 'getAllVaults',
    type: 'function',
    stateMutability: 'view',
    inputs: [],
    outputs: [{ name: '', type: 'address[]' }]
  }
] as const;

interface CacheEntry<T> {
  data: T;
  timestamp: number;
}

export class VaultProvider {
  private runtime: IAgentRuntime;
  private config: SeiConfig | null = null;
  private cache: Map<string, CacheEntry<any>> = new Map();
  private cacheTTL: number = 30000; // 30 seconds

  constructor(runtime: IAgentRuntime) {
    this.runtime = runtime;
  }

  private async getConfig(): Promise<SeiConfig> {
    if (!this.config) {
      this.config = await validateSeiConfig(this.runtime);
    }
    return this.config;
  }

  private getPublicClient() {
    const network = this.config?.SEI_NETWORK || 'sei-mainnet';
    const chain = seiChains[network] || seiChains['sei-mainnet'];
    return createPublicClient({
      chain,
      transport: http()
    });
  }

  private getCacheKey(method: string, ...args: any[]): string {
    return `${method}:${args.join(':')}`;
  }

  private getFromCache<T>(key: string): T | null {
    const entry = this.cache.get(key);
    if (entry && Date.now() - entry.timestamp < this.cacheTTL) {
      return entry.data as T;
    }
    return null;
  }

  private setCache<T>(key: string, data: T): void {
    this.cache.set(key, { data, timestamp: Date.now() });
  }

  /**
   * Get customer portfolio across all vaults
   */
  async getCustomerPortfolio(customerAddress: string): Promise<FormattedCustomerPortfolio[]> {
    const cacheKey = this.getCacheKey('getCustomerPortfolio', customerAddress);
    const cached = this.getFromCache<FormattedCustomerPortfolio[]>(cacheKey);
    if (cached) return cached;

    try {
      const config = await this.getConfig();
      const publicClient = this.getPublicClient();

      if (!config.CUSTOMER_DASHBOARD_ADDRESS) {
        throw new Error('CUSTOMER_DASHBOARD_ADDRESS not configured');
      }

      const result = await publicClient.readContract({
        address: config.CUSTOMER_DASHBOARD_ADDRESS as `0x${string}`,
        abi: CUSTOMER_DASHBOARD_ABI,
        functionName: 'getCustomerPortfolio',
        args: [customerAddress as `0x${string}`]
      });

      const formatted: FormattedCustomerPortfolio[] = (result as any[]).map(p => ({
        vaultAddress: p.vaultAddress,
        vaultName: p.vaultName,
        shareBalance: formatUnits(p.shareBalance, 18),
        shareValue: formatUnits(p.shareValue, 6), // USD with 6 decimals
        totalDeposited: formatUnits(p.totalDeposited, 6),
        totalWithdrawn: formatUnits(p.totalWithdrawn, 6),
        unrealizedGains: formatUnits(p.unrealizedGains, 6),
        depositTimestamp: Number(p.depositTimestamp),
        lockTimeRemaining: Number(p.lockTimeRemaining),
        canWithdraw: p.canWithdraw
      }));

      this.setCache(cacheKey, formatted);
      return formatted;
    } catch (error) {
      elizaLogger.error(`Failed to get customer portfolio: ${error}`);
      throw error;
    }
  }

  /**
   * Get vault metrics
   */
  async getVaultMetrics(vaultAddress: string): Promise<FormattedVaultMetrics> {
    const cacheKey = this.getCacheKey('getVaultMetrics', vaultAddress);
    const cached = this.getFromCache<FormattedVaultMetrics>(cacheKey);
    if (cached) return cached;

    try {
      const config = await this.getConfig();
      const publicClient = this.getPublicClient();

      if (!config.CUSTOMER_DASHBOARD_ADDRESS) {
        throw new Error('CUSTOMER_DASHBOARD_ADDRESS not configured');
      }

      const result = await publicClient.readContract({
        address: config.CUSTOMER_DASHBOARD_ADDRESS as `0x${string}`,
        abi: CUSTOMER_DASHBOARD_ABI,
        functionName: 'getVaultMetrics',
        args: [vaultAddress as `0x${string}`]
      }) as any;

      const formatted: FormattedVaultMetrics = {
        totalValueLocked: formatUnits(result.totalValueLocked, 6),
        totalShares: formatUnits(result.totalShares, 18),
        pricePerShare: formatUnits(result.pricePerShare, 18),
        apy: Number(result.apy) / 100, // Assuming APY is in basis points
        totalYieldGenerated: formatUnits(result.totalYieldGenerated, 6),
        managementFeeRate: Number(result.managementFeeRate) / 10000, // Basis points to percentage
        performanceFeeRate: Number(result.performanceFeeRate) / 10000,
        withdrawalFeeRate: Number(result.withdrawalFeeRate) / 10000
      };

      this.setCache(cacheKey, formatted);
      return formatted;
    } catch (error) {
      elizaLogger.error(`Failed to get vault metrics: ${error}`);
      throw error;
    }
  }

  /**
   * Get vault info
   */
  async getVaultInfo(vaultAddress: string): Promise<FormattedVaultInfo> {
    const cacheKey = this.getCacheKey('getVaultInfo', vaultAddress);
    const cached = this.getFromCache<FormattedVaultInfo>(cacheKey);
    if (cached) return cached;

    try {
      const publicClient = this.getPublicClient();

      const result = await publicClient.readContract({
        address: vaultAddress as `0x${string}`,
        abi: STRATEGY_VAULT_ABI,
        functionName: 'getVaultInfo'
      }) as any;

      const formatted: FormattedVaultInfo = {
        name: result.name,
        strategy: result.strategy,
        token0: result.token0,
        token1: result.token1,
        poolFee: Number(result.poolFee),
        totalSupply: formatUnits(result.totalSupply, 18),
        totalValueLocked: formatUnits(result.totalValueLocked, 6),
        isActive: result.isActive
      };

      this.setCache(cacheKey, formatted);
      return formatted;
    } catch (error) {
      elizaLogger.error(`Failed to get vault info: ${error}`);
      throw error;
    }
  }

  /**
   * Get current position
   */
  async getCurrentPosition(vaultAddress: string): Promise<Position> {
    const cacheKey = this.getCacheKey('getCurrentPosition', vaultAddress);
    const cached = this.getFromCache<Position>(cacheKey);
    if (cached) return cached;

    try {
      const publicClient = this.getPublicClient();

      const result = await publicClient.readContract({
        address: vaultAddress as `0x${string}`,
        abi: STRATEGY_VAULT_ABI,
        functionName: 'getCurrentPosition'
      }) as any;

      const position: Position = {
        tickLower: result.tickLower,
        tickUpper: result.tickUpper,
        liquidity: result.liquidity,
        tokensOwed0: result.tokensOwed0,
        tokensOwed1: result.tokensOwed1,
        feeGrowthInside0LastX128: result.feeGrowthInside0LastX128,
        feeGrowthInside1LastX128: result.feeGrowthInside1LastX128
      };

      this.setCache(cacheKey, position);
      return position;
    } catch (error) {
      elizaLogger.error(`Failed to get current position: ${error}`);
      throw error;
    }
  }

  /**
   * Get yield history
   */
  async getYieldHistory(vaultAddress: string, fromTimestamp: number): Promise<FormattedYieldHistory[]> {
    const cacheKey = this.getCacheKey('getYieldHistory', vaultAddress, fromTimestamp);
    const cached = this.getFromCache<FormattedYieldHistory[]>(cacheKey);
    if (cached) return cached;

    try {
      const config = await this.getConfig();
      const publicClient = this.getPublicClient();

      if (!config.CUSTOMER_DASHBOARD_ADDRESS) {
        throw new Error('CUSTOMER_DASHBOARD_ADDRESS not configured');
      }

      const result = await publicClient.readContract({
        address: config.CUSTOMER_DASHBOARD_ADDRESS as `0x${string}`,
        abi: CUSTOMER_DASHBOARD_ABI,
        functionName: 'getYieldHistory',
        args: [vaultAddress as `0x${string}`, BigInt(fromTimestamp)]
      });

      const formatted: FormattedYieldHistory[] = (result as any[]).map(h => ({
        timestamp: Number(h.timestamp),
        totalValue: formatUnits(h.totalValue, 6),
        yieldGenerated: formatUnits(h.yieldGenerated, 6),
        apy: Number(h.apy) / 100
      }));

      this.setCache(cacheKey, formatted);
      return formatted;
    } catch (error) {
      elizaLogger.error(`Failed to get yield history: ${error}`);
      throw error;
    }
  }

  /**
   * Calculate projected returns
   */
  async calculateProjectedReturns(
    vaultAddress: string,
    depositAmount: number,
    daysHeld: number
  ): Promise<FormattedProjectedReturns> {
    const cacheKey = this.getCacheKey('calculateProjectedReturns', vaultAddress, depositAmount, daysHeld);
    const cached = this.getFromCache<FormattedProjectedReturns>(cacheKey);
    if (cached) return cached;

    try {
      const config = await this.getConfig();
      const publicClient = this.getPublicClient();

      if (!config.CUSTOMER_DASHBOARD_ADDRESS) {
        throw new Error('CUSTOMER_DASHBOARD_ADDRESS not configured');
      }

      // Convert deposit amount to wei (assuming 6 decimals for USD)
      const depositAmountWei = BigInt(Math.floor(depositAmount * 1e6));

      const result = await publicClient.readContract({
        address: config.CUSTOMER_DASHBOARD_ADDRESS as `0x${string}`,
        abi: CUSTOMER_DASHBOARD_ABI,
        functionName: 'calculateProjectedReturns',
        args: [vaultAddress as `0x${string}`, depositAmountWei, BigInt(daysHeld)]
      }) as [bigint, bigint, bigint, bigint];

      const formatted: FormattedProjectedReturns = {
        projectedValue: formatUnits(result[0], 6),
        projectedYield: formatUnits(result[1], 6),
        managementFees: formatUnits(result[2], 6),
        withdrawalFees: formatUnits(result[3], 6)
      };

      this.setCache(cacheKey, formatted);
      return formatted;
    } catch (error) {
      elizaLogger.error(`Failed to calculate projected returns: ${error}`);
      throw error;
    }
  }

  /**
   * Get optimal deposit ratio
   */
  async getOptimalDepositRatio(vaultAddress: string): Promise<DepositRatio> {
    const cacheKey = this.getCacheKey('getOptimalDepositRatio', vaultAddress);
    const cached = this.getFromCache<DepositRatio>(cacheKey);
    if (cached) return cached;

    try {
      const config = await this.getConfig();
      const publicClient = this.getPublicClient();

      if (!config.CUSTOMER_DASHBOARD_ADDRESS) {
        throw new Error('CUSTOMER_DASHBOARD_ADDRESS not configured');
      }

      const result = await publicClient.readContract({
        address: config.CUSTOMER_DASHBOARD_ADDRESS as `0x${string}`,
        abi: CUSTOMER_DASHBOARD_ABI,
        functionName: 'getOptimalDepositRatio',
        args: [vaultAddress as `0x${string}`]
      }) as [bigint, bigint, bigint];

      const ratio: DepositRatio = {
        token0Ratio: result[0],
        token1Ratio: result[1],
        currentPrice: result[2]
      };

      this.setCache(cacheKey, ratio);
      return ratio;
    } catch (error) {
      elizaLogger.error(`Failed to get optimal deposit ratio: ${error}`);
      throw error;
    }
  }

  /**
   * Get all vaults from factory
   */
  async getAllVaults(): Promise<VaultWithAddress[]> {
    const cacheKey = this.getCacheKey('getAllVaults');
    const cached = this.getFromCache<VaultWithAddress[]>(cacheKey);
    if (cached) return cached;

    try {
      const config = await this.getConfig();
      const publicClient = this.getPublicClient();

      // Try getting from factory first
      let vaultAddresses: string[] = [];

      if (config.VAULT_FACTORY_ADDRESS) {
        try {
          const result = await publicClient.readContract({
            address: config.VAULT_FACTORY_ADDRESS as `0x${string}`,
            abi: VAULT_FACTORY_ABI,
            functionName: 'getAllVaults'
          });
          vaultAddresses = result as string[];
        } catch (e) {
          elizaLogger.warn('Could not get vaults from factory, using config addresses');
        }
      }

      // Fallback to configured addresses
      if (vaultAddresses.length === 0) {
        for (const [vaultName, configKey] of Object.entries(VAULT_CONFIG_KEYS)) {
          const address = config[configKey] as string;
          if (address) {
            vaultAddresses.push(address);
          }
        }
      }

      // Get info for each vault
      const vaults: VaultWithAddress[] = [];
      for (const address of vaultAddresses) {
        try {
          const info = await this.getVaultInfo(address);
          const metrics = await this.getVaultMetrics(address);

          vaults.push({
            ...info,
            address,
            apy: metrics.apy,
            riskLevel: this.getRiskLevel(info.name)
          });
        } catch (e) {
          elizaLogger.warn(`Could not get info for vault ${address}: ${e}`);
        }
      }

      this.setCache(cacheKey, vaults);
      return vaults;
    } catch (error) {
      elizaLogger.error(`Failed to get all vaults: ${error}`);
      throw error;
    }
  }

  /**
   * Get vault address by name
   */
  async getVaultAddressByName(vaultName: string): Promise<string | null> {
    const config = await this.getConfig();

    // Try exact match first
    const address = getVaultAddress(config, vaultName);
    if (address) return address;

    // Try fuzzy match
    const matched = matchVaultName(vaultName);
    if (matched) {
      return getVaultAddress(config, matched) || null;
    }

    return null;
  }

  /**
   * Get risk level based on vault name
   */
  private getRiskLevel(vaultName: string): 'Very Low' | 'Low' | 'Medium' | 'Medium-High' | 'High' {
    const lowerName = vaultName.toLowerCase();

    if (lowerName.includes('stable')) return 'Very Low';
    if (lowerName.includes('hedge')) return 'Low';
    if (lowerName.includes('delta neutral')) return 'Low';
    if (lowerName.includes('blue chip')) return 'Medium';
    if (lowerName.includes('concentrated')) return 'Medium';
    if (lowerName.includes('yield farming')) return 'Medium';
    if (lowerName.includes('arbitrage')) return 'Medium-High';
    if (lowerName.includes('hypergrowth')) return 'High';

    return 'Medium';
  }

  /**
   * Clear cache
   */
  clearCache(): void {
    this.cache.clear();
  }
}

// Singleton instance
let vaultProviderInstance: VaultProvider | null = null;

export const vaultProvider: Provider = {
  name: "vaultProvider",
  get: async (runtime: IAgentRuntime, message: Memory, state?: State) => {
    if (!vaultProviderInstance) {
      vaultProviderInstance = new VaultProvider(runtime);
      elizaLogger.info("Vault Provider initialized");
    }

    // Return provider description if no specific query
    if (!message?.content?.text) {
      return "Vault Provider: Query vault metrics, positions, and portfolio data for Yield Delta vaults on SEI blockchain.";
    }

    return null;
  }
};

export { vaultProviderInstance };
